import { FontAwesome5 } from "@expo/vector-icons";
import { Layout, ListItem, Text } from "@ui-kitten/components";
import { TouchableHighlight } from "react-native";

import BottomBar from "../BottomBar";

export default function TextCard({
  navigation,
  tags,
  title,
  body,
  emoji,
  _id,
  searchTerm,
  searchedTag,
  pinnedIcon,
  isUrl = false,
}) {
  const renderTextBody = () => {
    const text = body;

    if (text.indexOf("\n") > 10 && text.indexOf("\n") < 50) {
      return `${text.split("\n")[0]}...`;
    } if (text.length > 50) {
      return `${text.substring(0, 50)}...`;
    }
    return text;

  };

  const renderUrl = () => {
    return (
      <Text style={{ color: "grey" }}>
        <FontAwesome5 name="link" />
        {` `}
        {body.length > 30 ? `${body.slice(0, 30)}...` : body}
      </Text>
    );
  };

  return (
    <TouchableHighlight
      onPress={() => {
        navigation.navigate("SinglePost", { _id, searchTerm, searchedTag });
      }}
    >
      <Layout>
        {pinnedIcon}

        <ListItem
          disabled
          accessoryLeft={() => (
            <Text style={{ fontSize: 35, marginRight: 15 }}>{emoji}</Text>
          )}
          style={{
            paddingLeft: 15,
            paddingTop: 25,
            paddingBottom: 0,
          }}
          title={() => (
            <Text style={{ fontSize: 20, paddingBottom: 5 }}>{title}</Text>
          )}
          description={() => (
            <Text style={{ color: "grey" }}>
              {isUrl ? renderUrl() : renderTextBody()}
            </Text>
          )}
        />

        <BottomBar
          tags={tags}
          _id={_id}
          searchTerm={searchTerm}
          searchedTag={searchedTag}
        />
      </Layout>
    </TouchableHighlight>
  );
}
