import { A } from "@expo/html-elements";
import { Input, Text } from "@ui-kitten/components";
import { Dimensions } from "react-native";

import { selectDarkMode } from "../../../app/appDataSlice";
import { useAppSelector } from "../../../app/hooks";
import { validURL } from "../../../functions";

const windowWidth = Dimensions.get("window").width;

export default function SinglePostBody({ type, body, handleLikePost }) {
  const darkMode = useAppSelector(selectDarkMode);

  if (type === "contentText") {
    return (
      <Input
        scrollEnabled={false}
        multiline
        disabled
        value={body}
        style={{
          backgroundColor: "transparent",
          borderWidth: 0,
        }}
        textStyle={{ color: darkMode ? "white" : "black" }}
      />
    );
  } if (type === "contentUrl") {
    if (validURL(body)) {
      return (
        <A style={{ color: "blue" }} href={body}>
          {body}
        </A>
      );
    }
    return <Text>{body}</Text>;

  }
  return <></>;
}
