import { FontAwesome5 } from "@expo/vector-icons";
import { Layout, Text } from "@ui-kitten/components";
import {
  useEffect, useMemo, useRef, useState
} from "react";
import {
  StyleSheet,
  TouchableOpacity,
  LayoutAnimation,
  UIManager,
  Platform,
  Animated,
} from "react-native";

import ClassListItemItem from "./ClassListItemItem";
import useTraceUpdate from "../../hooks/useTraceUpdate";

if (
  Platform.OS === "android"
  && UIManager.setLayoutAnimationEnabledExperimental
) {
  UIManager.setLayoutAnimationEnabledExperimental(true);
}

export default function Item({
  courseName,
  masterCourseCode,
  schedule,
  menuOpen,
  setIsMenuOpen,
  darkMode,
  handleClassSelected,
  handleAddClass,
  handleRemoveClass,
  inItem,
}) {
  const memoizedItem = useMemo(() => {
    return (
      <Layout
        style={{
          borderRadius: 20,
          opacity: 1,
          marginTop: 7,
          marginRight: 9,
          marginLeft: 9,
        }}
      >
        {/* Name and master coursecode */}
        <Layout style={{ marginHorizontal: 20 }}>
          <Layout>
            <TouchableOpacity
              onPress={() => {
                handleClassSelected(masterCourseCode);
              }}
            >
              <Layout style={styles.item}>
                <Text style={styles.title}>{courseName}</Text>
                <Text>{masterCourseCode}</Text>
              </Layout>
            </TouchableOpacity>

            {/* Dropdown Menu Toggle */}
            <TouchableOpacity
              style={styles.buttonContainer}
              onPress={() => {
                if (menuOpen === masterCourseCode) {
                  setIsMenuOpen("");
                  LayoutAnimation.configureNext({
                    duration: 730,
                    create: { type: "easeInEaseOut", property: "opacity" },
                    update: { type: "spring", springDamping: 0.8 },
                  });
                } else {
                  setIsMenuOpen(masterCourseCode);
                  LayoutAnimation.configureNext({
                    duration: Platform.OS === "android" ? 500 : 730,
                    create: { type: "easeInEaseOut", property: "opacity" },
                    update: { type: "spring", springDamping: 0.8 },
                  });
                }
              }}
            >
              <Layout style={styles.button}>
                <FontAwesome5
                  name={
                    menuOpen === masterCourseCode
                      ? "chevron-up"
                      : "chevron-down"
                  }
                  size={20}
                  color={darkMode ? "white" : "black"}
                />
              </Layout>
            </TouchableOpacity>
          </Layout>

          {/* Dropdown Menu */}
          {menuOpen === masterCourseCode && (
            <Layout>
              {schedule.map(
                ({
                  _id, courseCode, seatsAvailable, seatsFilled, added
                }) => (
                  <ClassListItemItem
                    key={_id}
                    courseCode={courseCode}
                    seatsAvailable={seatsAvailable}
                    seatsFilled={seatsFilled}
                    handleAddClass={handleAddClass}
                    handleRemoveClass={handleRemoveClass}
                    darkMode={darkMode}
                    added={added}
                    masterCourseCode={masterCourseCode}
                  />
                ),
              )}
            </Layout>
          )}
        </Layout>
      </Layout>
    );
  }, [menuOpen, inItem, darkMode]);

  // useTraceUpdate(memoizedItem);
  return memoizedItem;
}

