import { apiSlice } from "./apiSlice";
import {
  AddedClasses,
  CourseTermAddedClasses,
  UserData,
} from "../../app/types";

export const usersApiSlice = apiSlice.injectEndpoints({
  overrideExisting: true,
  endpoints: (builder) => ({
    getUserInfo: builder.query({
      providesTags: ["User"],
      query: (userKey: string) => ({
        url: "/users/me",
        headers: {
          userKey,
        },
      }),
      transformErrorResponse: (response) => {
        if (response.status === 401) {
          return { error: "Unauthorized" };
        }
        return response;
      },
      transformResponse: ({ classes, ...rest }) => {
        const addedClasses = {
          length: 0,
          classes: {} as { [courseTerm: string]: CourseTermAddedClasses },
        } as AddedClasses;

        try {
          for (const { courseCode, courseTerm } of classes) {
            let school = courseCode.split("-");
            school = school[school.length - 2].split(" ");
            school = school[school.length - 1];
            const masterCourseCode = `${courseCode.split(" ").slice(0, 2).join(" ")} ${school}`;
            if (courseTerm in addedClasses.classes) {
              if (masterCourseCode in addedClasses.classes[courseTerm]) {
                addedClasses.classes[courseTerm][masterCourseCode].push(
                  courseCode,
                );
              } else {
                addedClasses.classes[courseTerm][masterCourseCode] = [
                  courseCode,
                ];
              }
            } else {
              addedClasses.classes[courseTerm] = {
                [masterCourseCode]: [courseCode],
              };
            }

            addedClasses.length++;
          }
        } catch (e) {
          console.warn("Error Adding Classes To State", e);
        }

        return { addedClasses, ...rest } as UserData;
      },
    }),
    login: builder.mutation({
      query: ({ userName, password, isGuest }) => ({
        url: `/users/login`,
        params: isGuest
          ? {
            userName,
            password,
            isGuest,
          }
          : {
            userName,
            password,
          },
      }),
    }),
    trackLogin: builder.mutation({
      query: (userKey) => ({
        url: `/analytics/login/openedApp`,
        method: "POST",
        headers: {
          userKey,
        },
        responseHandler: (response) => response.text(),
      }),
    }),
    deleteAccount: builder.mutation({
      query: ({ userKey, userName }) => ({
        url: "/users",
        method: "DELETE",
        headers: {
          userKey,
        },
        params: {
          userName,
        },
      }),
    }),
    editUser: builder.mutation({
      invalidatesTags: ["User"],
      query: ({ userKey, userName, fields }) => ({
        url: `/users/${userName}`,
        method: "PATCH",
        headers: {
          "content-type": "application/json",
          userKey,
        },
        body: fields,
      }),
    }),
    getCollegeList: builder.query<any, void>({
      query: () => "/randomShit/getCollegeList",
    }),
    signupUser: builder.mutation({
      query: (body) => ({
        url: "/users",
        method: "POST",
        body,
        responseHandler: (response) => response.text(),
      }),
    }),
    verifySignup: builder.mutation({
      query: (code) => ({
        url: "/users/verification",
        method: "POST",
        body: { code },
      }),
    }),
    resetPassword: builder.mutation({
      query: (email) => ({
        url: "/users/forgotPassword",
        params: {
          email,
        },
        responseHandler: (response) => response.text(),
      }),
    }),
    addExpoPushToken: builder.mutation({
      query: ({ userName, expoPushToken, userKey }) => ({
        url: `/users/addExpoPushKey/${userName}`,
        method: "PATCH",
        headers: {
          userKey,
        },
        body: {
          expoPushToken,
        },
      }),
    }),
  }),
});

export const {
  useTrackLoginMutation,
  useGetUserInfoQuery,
  useLazyGetUserInfoQuery,
  useLoginMutation,
  useDeleteAccountMutation,
  useEditUserMutation,
  useGetCollegeListQuery,
  useSignupUserMutation,
  useVerifySignupMutation,
  useResetPasswordMutation,
  useAddExpoPushTokenMutation,
} = usersApiSlice;
