import { FontAwesome5 } from "@expo/vector-icons";
import { useFocusEffect, useIsFocused } from "@react-navigation/native";
import { Divider, Layout, Text } from "@ui-kitten/components";
import React, { useEffect, useMemo, useState } from "react";
import { TouchableOpacity, Dimensions } from "react-native";
import Toast from "react-native-root-toast";

import { selectDarkMode, selectUserKey } from "../../../app/appDataSlice";
import { useAppSelector } from "../../../app/hooks";
import ModalContainer from "../../../components/ModalContainer";
import { MAX_WIDTH_WEB } from "../../../constants/discover";
import { FormatDate, FormatTimeRange } from "../../../functions";
import { useEditUserMutation, useGetUserInfoQuery } from "../../api/usersApi";
import BottomBar from "../BottomBar";
import EventBody from "../EventBody";
import ImageComponent from "../ImageComponent";

const windowWidth = Dimensions.get("window").width;

interface EventCardProps {
  title: string;
  location: string;
  date: string;
  startTime: string;
  endTime: string;
  pinnedIcon: JSX.Element;
  _id: string;
  postAuthor: string;
  userName: string;
  hasLiked: boolean;
  navigation: any;
  searchTerm?: string;
  searchedTag?: string;
  tags?: string[];
  contentURL?: string;
  contentText?: string;
  contentImage?: string;
  host?: string;
}

export default function EventCard({
  title,
  location,
  date,
  startTime,
  endTime,
  pinnedIcon,
  tags,
  contentURL,
  contentText,
  contentImage,
  host,
  hasLiked,
  _id,
  searchTerm,
  searchedTag,
  postAuthor,
  userName,
  navigation,
}: EventCardProps) {
  const darkMode = useAppSelector(selectDarkMode);
  const userKey = useAppSelector(selectUserKey);
  const { data: userData } = useGetUserInfoQuery(useAppSelector(selectUserKey));
  const [eventPopUp, setEventPopUp] = useState(false);
  const [isAdded, setIsAdded] = useState(false);
  const [editUser] = useEditUserMutation();

  useEffect(() => {
    setIsAdded(
      userData.customEvents.some((customEvent) => {
        return (
          customEvent.name === title
          && customEvent.startTime == startTime
          && customEvent.endTime == endTime
          && customEvent.date == date
        );
      }),
    );
  }, [useIsFocused()]); // this forces just the button component to re-render when the focus changes

  function handleEventSyncWithServer(isAdd: boolean) {
    editUser({
      userKey,
      userName: userData.userName,
      fields: JSON.stringify(
        isAdd
          ? {
            addCustomEvent: {
              name: title,
              startTime: new Date(startTime).toTimeString().slice(0, 5),
              endTime: new Date(endTime).toTimeString().slice(0, 5),
              days: [],
              date,
            },
          }
          : {
            removeCustomEvent: {
              name: title,
              startTime: new Date(startTime).toTimeString().slice(0, 5),
              endTime: new Date(endTime).toTimeString().slice(0, 5),
              days: [],
              date,
            },
          },
      ),
    })
      .unwrap()
      .then(async () => {
        setIsAdded(isAdd);
      })
      .catch(() => {
        Toast.show("Failed to add event.\nPlease try again later", {
          duration: Toast.durations.LONG,
          opacity: 1,
          position: -100,
          backgroundColor: "grey",
          shadow: false,
        });
      });
  }

  function toggleAdditionOfEvent() {
    handleEventSyncWithServer(!isAdded);
  }

  return (
    <Layout>
      {pinnedIcon}
      <Layout style={{ flex: 1, alignItems: "center" }}>
        <TouchableOpacity
          onPress={() => {
            navigation.navigate("SinglePost", { _id, searchTerm, searchedTag });
          }}
          activeOpacity={0.9}
          style={{
            flex: 1,
            alignItems: "center",
            paddingVertical: 10,
          }}
        >
          <Layout
            style={{
              width: windowWidth * 0.9,
              maxWidth: MAX_WIDTH_WEB,
              paddingVertical: 10,
              flexDirection: "row",
            }}
          >
            <Text style={{ fontSize: 20 }}>{title}</Text>
            <TouchableOpacity
              style={{ alignSelf: "flex-end", marginLeft: "auto" }}
              onPress={() => {
                toggleAdditionOfEvent();
              }}
            >
              <FontAwesome5
                name={isAdded ? "times" : "plus"}
                size={23}
                color={darkMode ? "white" : "black"}
              />
            </TouchableOpacity>
          </Layout>

          <EventBody
            contentURL={contentURL}
            contentText={contentText}
            contentImage={contentImage}
            host={host}
            title={title}
            location={location}
            date={date}
            startTime={startTime}
            endTime={endTime}
            _id={_id}
            postAuthor={postAuthor}
            userName={userName}
            hasLiked={hasLiked}
          />

          <BottomBar
            tags={tags}
            _id={_id}
            searchTerm={searchTerm}
            searchedTag={searchedTag}
          />
        </TouchableOpacity>
      </Layout>
    </Layout>
  );
}
