function convertTo12HourTime(time) {
  if (time.length !== 5) {
    time = `0${time}`;
  }
  let hours = time.slice(0, 2);
  const minutes = time.slice(3, 5);
  let ampm = Number.parseInt(hours) > 12 ? "pm" : "am";
  hours %= 12;
  if (hours === 0) {
    hours = 12;
    ampm = "pm";
  }
  return `${hours}:${minutes}${ampm}`;
}

function getMinutesDiff(startTime, endTime) {
  if (endTime > startTime) {
    return (endTime - startTime) * 0.7;
  } if (startTime === endTime) {
    return 0;
  }
  return (startTime - endTime) * -0.7;

}

function Calendar(userData) {
  const allClassData = useAppSelector(selectClassesList);
  const { data: currentSection, isLoading: isCurrentSectionLoading } = useGetCurrentSectionQuery();
  const darkMode: boolean = useAppSelector(selectDarkMode);
  const calendarFun = new CalendarFunctions();
  const days = [
    { day: "Mon", id: "Monday" },
    { day: "Tues", id: "Tuesday" },
    { day: "Wed", id: "Wednesday" },
    { day: "Thurs", id: "Thursday" },
    { day: "Fri", id: "Friday" },
    { day: "Sat", id: "Saturday" },
    { day: "Sun", id: "Sunday" },
  ];
  const times = [
    "12:00 AM",
    "1:00 AM",
    "2:00 AM",
    "3:00 AM",
    "4:00 AM",
    "5:00 AM",
    "6:00 AM",
    "7:00 AM",
    "8:00 AM",
    "9:00 AM",
    "10:00 AM",
    "11:00 AM",
    "12:00 PM",
    "1:00 PM",
    "2:00 PM",
    "3:00 PM",
    "4:00 PM",
    "5:00 PM",
    "6:00 PM",
    "7:00 PM",
    "8:00 PM",
    "9:00 PM",
    "10:00 PM",
    "11:00 PM",
  ];

  function renderDashes() {
    const final = [];
    for (let i = 0; i < 24; i++) {
      final.push(
        <Layout
          style={{
            width: 46,
            marginLeft: 9,
            top: 41.7 * i,
            position: "absolute",
            height: 41.7,
            borderBottomWidth: 0.7,
            borderBottomColor: darkMode ? "rgb(68,68,68)" : "rgb(204,204,204)",
          }}
        />,
      );
    }
    return final;
  }

  const renderClasses = (elem) => {
    const classes = [];
    for (const course of userData) {
      if (course.courseTerm === currentSection) {
        const data = calendarFun.getFullClassData(
          course.courseCode,
          allClassData,
        );
        const data2 = data.times.find(
          (time) => time.courseCode === course.courseCode,
        );
        for (const day in data2.daysOfTheWeek) {
          if (data2.daysOfTheWeek[day] === elem.id) {
            classes.push(renderItem(data, course.courseCode, () => {}));
          }
        }
      }
    }

    return classes;
  };

  const topBar = useRef(null);

  return (
    <SafeAreaView
      style={{
        marginTop: 0,
        marginLeft: 0,
        paddingBottom: 15,
        marginBottom: 20,
      }}
    >
      {/* Times */}
      <ScrollView
        horizontal
        snapToInterval={63}
        showsHorizontalScrollIndicator={false}
        style={{ marginLeft: 75 }}
        ref={topBar}
      >
        <View style={{ flexDirection: "row" }}>
          {days.map((elem) => {
            return (
              <Layout>
                <Layout
                  style={{
                    borderColor: darkMode ? "#7a7a7a" : "#525252",
                    borderBottomWidth: 1,
                    width: "100%",
                  }}
                >
                  <Text
                    style={{
                      textAlign: "center",
                      color: darkMode ? "#7a7a7a" : "#525252",
                      width: 63,
                    }}
                  >
                    {elem.day}
                  </Text>
                </Layout>
              </Layout>
            );
          })}
        </View>
      </ScrollView>
      <ScrollView
        contentOffset={{ x: 0, y: 292 }}
        showsVerticalScrollIndicator={false}
        bounces={false}
      >
        <View style={{ flexDirection: "row" }}>
          <View
            style={{
              justifyContent: "space-around",
              height: 1000,
            }}
          >
            {times.map((time) => {
              return (
                <Text
                  style={{
                    textAlign: "center",
                    color: darkMode ? "#7a7a7a" : "#525252",
                    paddingTop: "15%",
                    height: 43,
                    width: 75,
                    borderWidth: 1,
                    borderColor: darkMode ? "#7a7a7a" : "#525252",
                  }}
                >
                  {time}
                </Text>
              );
            })}
          </View>
          {/* Classes */}
          <ScrollView
            horizontal
            snapToInterval={63}
            decelerationRate="normal"
            showsHorizontalScrollIndicator={false}
            bounces={false}
            scrollEventThrottle={1}
            onScroll={(e) => topBar.current.scrollTo({
              x: e.nativeEvent.contentOffset.x,
              animated: false,
              useNativeDriver: true,
            })}
            onMomentumScrollBegin={(e) => topBar.current.scrollTo({
              x: e.nativeEvent.contentOffset.x,
              animated: false,
              useNativeDriver: true,
            })}
          >
            <View style={{ flexDirection: "row" }}>
              {days.map((elem) => {
                return (
                  <Layout>
                    {renderDashes()}
                    <Layout key={elem.day} style={{ width: 63 }}>
                      {renderClasses(elem)}
                    </Layout>
                    <Layout
                      style={{
                        height: "100%",
                        width: 1,
                        backgroundColor: darkMode ? "#7a7a7a" : "#525252",
                      }}
                    />
                  </Layout>
                );
              })}
            </View>
          </ScrollView>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
}

