import { A } from "@expo/html-elements";
import { FontAwesome5 } from "@expo/vector-icons";
import { Divider, Layout, Text } from "@ui-kitten/components";
import { useMemo } from "react";
import { TouchableOpacity, Dimensions } from "react-native";

import BottomBar from "./BottomBar";
import ImageComponent from "./ImageComponent";
import { MAX_WIDTH_WEB } from "../../constants/discover";
import { FormatDate, FormatTimeRange } from "../../functions";

const windowWidth = Dimensions.get("window").width;

interface EventCardProps {
  title: string;
  location: string;
  date: string;
  startTime: string;
  endTime: string;
  _id: string;
  postAuthor: string;
  userName: string;
  hasLiked: boolean;
  searchTerm?: string;
  searchedTag?: string;
  tags?: string[];
  contentURL?: string;
  contentText?: string;
  contentImage?: string;
  host?: string;
  truncated?: boolean;
}

export default function EventBody({
  title,
  location,
  date,
  startTime,
  endTime,
  contentURL,
  contentText,
  contentImage,
  host,
  hasLiked,
  _id,
  searchTerm,
  searchedTag,
  postAuthor,
  userName,
  truncated = true,
}: EventCardProps) {
  const eventInfo = useMemo(
    () => [
      {
        title: "Location",
        value: location,
      },
      {
        title: "Date",
        value: FormatDate(date),
      },
      {
        title: "Time",
        value: FormatTimeRange(startTime, endTime),
      },
      {
        title: "Host",
        value: host,
      },
      {
        title: "Description",
        value: contentText,
      },
    ],
    [location, date, startTime, endTime, host, contentURL, contentText],
  );

  const renderUrl = (link: string) => {
    return (
      <Layout
        style={{
          flexDirection: "row",
          width: windowWidth * 0.9,
          maxWidth: MAX_WIDTH_WEB,
          paddingVertical: 5,
        }}
      >
        <Text category="s1">Link: </Text>
        {truncated ? (
          <Text category="p1">
            {link.length > 30 ? `${link.slice(0, 30)}...` : link}
          </Text>
        ) : (
          <A>
            <Text>{link}</Text>
          </A>
        )}
      </Layout>
    );
  };

  return (
    <Layout style={{ flex: 1, alignItems: "center" }}>
      {contentImage ? (
        <ImageComponent
          apiInfo={{
            _id,
            title,
            postAuthor,
            userName,
            searchTerm,
            searchedTag,
          }}
          imageId={contentImage}
          hasLiked={hasLiked}
        />
      ) : (
        <Divider />
      )}

      {/* Event Info  */}
      {eventInfo.map((info, index) => {
        if (info.value) {
          return (
            <Layout
              key={index}
              style={{
                flexDirection: "row",
                width: windowWidth * 0.9,
                maxWidth: MAX_WIDTH_WEB,
                paddingVertical: 5,
              }}
            >
              <Text category="s1">
                {info.title}
                {`: `}
                <Text category="p1">
                  {truncated
                    ? info.value.length > 30
                      ? `${info.value.slice(0, 30)}...`
                      : info.value
                    : info.value}
                </Text>
              </Text>
            </Layout>
          );
        }
      })}

      {contentURL ? renderUrl(contentURL) : null}
    </Layout>
  );
}
