import { Layout, useTheme } from "@ui-kitten/components";
import { useRef } from "react";
import { Dimensions, Image } from "react-native";

import { selectUserKey } from "../../app/appDataSlice";
import { useAppSelector } from "../../app/hooks";
import DoubleTap from "../../components/DoubleTap";
import DoubleTapHeart from "../../components/DoubleTapHeart";
import { MAX_WIDTH_WEB } from "../../constants/discover";
import { getImageUrl } from "../../functions";
import { useLikePostMutation } from "../api/discoverApi";

const windowWidth = Dimensions.get("window").width;

export default function ImageComponent({ imageId, hasLiked, apiInfo }) {
  const theme = useTheme();
  const [likePost] = useLikePostMutation();
  const userKey = useAppSelector(selectUserKey);
  const likeRef = useRef(null);

  const handlePressLike = async () => {
    likeRef.current?.animate();

    if (hasLiked) {
      return;
    }
    try {
      await likePost({
        userKey,
        tag: apiInfo.searchedTag,
        ...apiInfo,
      }).unwrap();
    } catch (error) {
      console.log("Error liking post:", error);
    }
  };

  return (
    <DoubleTap delay={1200} onDoubleTap={handlePressLike}>
      <Layout style={{ flex: 1, alignItems: "center" }}>
        <Image
          source={{
            uri: getImageUrl(imageId),
          }}
          style={{
            borderRadius: 5,
            width: windowWidth * 0.9,
            height: windowWidth * 0.9,
            maxHeight: MAX_WIDTH_WEB,
            maxWidth: MAX_WIDTH_WEB,
            marginBottom: 10,
            resizeMode: "contain",
          }}
        />
        <DoubleTapHeart
          style={{
            position: "absolute",
            top: "35%",
            right: "30%",
            shadowColor: "#000",
            shadowOffset: {
              width: 0,
              height: 3,
            },
            shadowOpacity: 0.29,
            shadowRadius: 4.65,

            elevation: 7,
          }}
          ref={likeRef}
          iconStyle={{ color: theme["color-primary-500"] }}
        />
      </Layout>
    </DoubleTap>
  );
}
