import {
  Layout, Input, Button, Icon, useTheme
} from "@ui-kitten/components";
import { useState } from "react";
import {
  Keyboard,
  TouchableOpacity,
  LayoutAnimation,
  TouchableWithoutFeedback,
} from "react-native";

import { selectDarkMode } from "../app/appDataSlice";
import { useAppSelector } from "../app/hooks";

type SearchBarProps = {
  onChangeText: (text: string) => void;
  searchTerm: string;
  placeholder?: string;
  accessoryLeft?: any;
  inputProps?: any;
  inputStyle?: any;
  inputRef?: any;
  cancelButtonAction?: () => void;
  onSubmitEditing?: () => void;
  alwaysShowCancelButton?: boolean;
  cancelButtonStatus?:
    | "basic"
    | "primary"
    | "success"
    | "info"
    | "warning"
    | "danger";
};

export default function SearchBar({
  onChangeText,
  searchTerm,
  accessoryLeft,
  placeholder,
  inputRef,
  inputStyle,
  inputProps,
  cancelButtonAction = () => {},
  onSubmitEditing = () => {},
  cancelButtonStatus = "warning",
  alwaysShowCancelButton = false,
}: SearchBarProps) {
  const [selected, setSelected] = useState(false);

  const darkMode = useAppSelector(selectDarkMode);
  const theme = useTheme();

  const select = () => {
    setSelected(true);
    if (!alwaysShowCancelButton) {
      LayoutAnimation.configureNext(
        LayoutAnimation.create(100, "linear", "scaleX"),
      );
    }
  };

  const deselect = () => {
    setSelected(false);
    if (!alwaysShowCancelButton) {
      LayoutAnimation.configureNext(
        LayoutAnimation.create(100, "linear", "opacity"),
      );
    }
  };

  return (
    <Layout
      style={{
        backgroundColor: "transparent",
        flexDirection: "row",
      }}
    >
      {accessoryLeft}

      <Input
        style={{
          ...inputStyle,
          borderRadius: 20,
          flex: 1,
          borderWidth: 1,
          borderColor: darkMode
            ? theme["color-basic-500"]
            : theme["color-basic-100"],
        }}
        placeholderTextColor={theme["color-basic-500"]}
        accessoryRight={
          selected && searchTerm.length > 0
            ? (props) => (
              <TouchableOpacity onPress={() => onChangeText("")}>
                <Icon {...props} name="times" pack="fontAwesome" />
              </TouchableOpacity>
            )
            : null
        }
        onFocus={select}
        onBlur={deselect}
        placeholder={placeholder}
        value={searchTerm}
        onChangeText={(text) => {
          onChangeText(text);
        }}
        onSubmitEditing={onSubmitEditing}
        ref={inputRef}
        autoCorrect={false}
        {...inputProps}
      />

      {alwaysShowCancelButton || selected ? (
        <Button
          size="small"
          status={cancelButtonStatus}
          appearance="ghost"
          onPress={() => {
            Keyboard.dismiss();
            cancelButtonAction();
          }}
        >
          Cancel
        </Button>
      ) : null}
    </Layout>
  );
}
