import { FontAwesome } from "@expo/vector-icons";
import { Layout, Text, useTheme } from "@ui-kitten/components";
import React from "react";
import { TouchableOpacity, Platform, UIManager } from "react-native";

import { selectDarkMode, selectUserKey } from "../../app/appDataSlice";
import { useAppSelector } from "../../app/hooks";
import withPreventDoubleClick from "../../components/withPreventDoubleClick";
import {
  discoverApiSlice,
  postsAdapter,
  postsSelectors,
  useLikePostMutation,
} from "../api/discoverApi";
import { usersApiSlice } from "../api/usersApi";

const DebounceTouchable = withPreventDoubleClick(TouchableOpacity, 600);

export default function BottomBar({
  tags,
  _id,
  commentIconShown = true,
  searchTerm = undefined,
  searchedTag = undefined,
}) {
  const userKey = useAppSelector(selectUserKey);
  const darkMode = useAppSelector(selectDarkMode);
  const theme = useTheme();

  const { data: userData } = usersApiSlice.endpoints.getUserInfo.useQueryState(userKey);
  const [likePost] = useLikePostMutation();

  const post = discoverApiSlice.endpoints.getDiscoverPosts.useQueryState(
    {
      pageNumber: 1,
      userKey,
      userName: userData.userName,
      searchTerm,
      tag: searchedTag,
    },
    {
      selectFromResult: (result) => postsSelectors.selectById(
        result.data ?? postsAdapter.getInitialState(),
        _id,
      ),
    },
  );

  const onPressLike = async () => {
    if (!post) return;
    try {
      await likePost({
        userKey,
        _id,
        title: post.title,
        postAuthor: post.userName,
        userName: userData.userName,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.log("Error liking post:", error);
    }
  };

  return (
    <Layout
      style={{
        width: "100%",
        flexDirection: "row",
        paddingVertical: 5,
        paddingHorizontal: 15,
      }}
    >
      <Layout
        style={{
          flexDirection: "row",
          flexWrap: "wrap",
          flex: 6,
        }}
      >
        {/* tags */}
        {tags.map((tag: string, index: number) => (
          <Layout
            key={index}
            level="3"
            style={{
              padding: 5,
              borderRadius: 5,
              marginRight: 5,
              marginTop: 2,
            }}
          >
            <Text style={{ fontSize: 12 }}>{tag}</Text>
          </Layout>
        ))}
      </Layout>

      {/* Icons */}
      <Layout
        style={{
          flexDirection: "row",
          flex: 2,
          justifyContent: "space-evenly",
          alignItems: "center",
        }}
      >
        <FontAwesome
          style={{ flex: 2, opacity: commentIconShown ? 1 : 0 }}
          name="comments-o"
          size={20}
          color={darkMode ? "#fff" : "#000"}
        />
        {/* Likes */}
        {/* @ts-ignore */}
        <DebounceTouchable
          style={{
            flexDirection: "row",
            flex: 3,
            justifyContent: "space-evenly",
            alignItems: "center",
          }}
          onPress={onPressLike}
        >
          <Text
            style={{
              fontSize: 15,
              paddingTop: 1,
            }}
          >
            {post?.numLikes}
          </Text>
          <FontAwesome
            style={{ alignSelf: "center" }}
            size={17}
            color={
              post?.hasLiked
                ? theme["color-primary-500"]
                : darkMode
                  ? "#fff"
                  : "#000"
            }
            name={post?.hasLiked ? "heart" : "heart-o"}
          />
        </DebounceTouchable>
      </Layout>
      {/* @ts-ignore */}
    </Layout>
  );
}
