import { Layout, Text } from "@ui-kitten/components";
import { useEffect, useState } from "react";
import {
  Dimensions,
  Image,
  ImageBackground,
  Platform,
  StyleSheet,
  TouchableHighlight,
} from "react-native";

import becomeSponsor from "../../../../assets/sponsorPage/become-sponsor.png";
import { selectDarkMode } from "../../../app/appDataSlice";
import { useAppSelector } from "../../../app/hooks";

const windowWidth = Dimensions.get("window").width;

export default function SponsorCard({ data, navigation }) {
  const [imageLink, setImageLink] = useState({ uri: data.logo });
  const webWidth = Dimensions.get("window").width * 0.3;
  const hasCoupon = !!data.coupons[0];

  return (
    <TouchableHighlight
      style={{ flex: 1 }}
      onPress={() => {
        if (hasCoupon) {
          navigation.navigate("SponsorPageDiscover", { data });
        }
      }}
    >
      <Layout style={{ flex: 1 }}>
        <Text category="label" style={{ marginLeft: 20 }}>
          Ad
        </Text>

        <Layout
          style={{
            flexDirection: "row",
            justifyContent: "space-between",
          }}
        >
          <Layout
            style={{ flex: 1, alignItems: "center", justifyContent: "center" }}
          >
            <Image
              source={imageLink}
              onError={() => {
                setImageLink(becomeSponsor);
              }}
              style={{
                height: hasCoupon
                  ? Platform.OS === "web"
                    ? 200
                    : windowWidth / 2 - 20
                  : Platform.OS === "web"
                    ? 200
                    : windowWidth * 0.8,
                width: hasCoupon
                  ? Platform.OS === "web"
                    ? "20%"
                    : windowWidth / 2 - 20
                  : Platform.OS === "web"
                    ? "20%"
                    : windowWidth * 0.8,
                resizeMode: "contain",
              }}
            />
            {imageLink?.uri && <Text>{data.sponsorName}</Text>}
          </Layout>

          {data.coupons[0] && imageLink?.uri && (
            <Layout style={{ flex: 1, alignItems: "center" }}>
              <ImageBackground
                resizeMode="contain"
                source={require("../../../../assets/sponsorPage/coupon.png")}
                style={{
                  width: windowWidth / 2 - 20,
                  height: 200,
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <Text
                  style={{
                    fontSize: 13,
                    paddingBottom: 7,
                    paddingRight: 7,
                    marginHorizontal: 25,
                  }}
                >
                  {data.coupons[0].couponDescription}
                </Text>
              </ImageBackground>
            </Layout>
          )}
        </Layout>
      </Layout>
    </TouchableHighlight>
  );
}
